body {
    font-family: cursive;
    margin: 0;
    padding: 0;
    background-color: #fff;
}

/* Header */
.header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color:tomato;
    color: white; 
}

header h1 {
    font-size: 2em;
    font-weight: bold;
    margin: 0;
    color: white; 
}

.contact-info,
.email-info {
    width: 200px; 
    font-size: 0.9em;
    text-align: center;
    color: white;
}


.home-link {
    text-decoration: none;
    color: white;
    font-weight: bold;
}

.contact-info,
.email-info {
    font-size: 0.9em;
    text-align: center;
}

/* Navigation */
nav {
    background-color: white;
    border-bottom: 2px solid tomato; /* Border under the navbar */
}

nav ul {
    list-style: none;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 10px 0;
}

nav li {
    margin: 0;
    padding: 0 15px;
    border-right: 1px solid tomato; /* Vertical border between links */
}

nav li:last-child {
    border-right: none; /* No border after the last item */
}

nav a {
    text-decoration: none;
    color: tomato;
    font-weight: bold;
    font-size: 1em;
}

nav a:hover {
    text-decoration: underline;
}

/* Hours Section */
.hours {
    padding: 20px;
    text-align: center;
    background-color:white !important; /* Force red background */
    color:tomato !important;          /* Force white text */
}

.hours h2 {
    text-decoration: underline;
    color:tomato !important;
    background-color: transparent !important;
}

.hours ul {
    list-style: none;
    padding: 0;
}

.hours li {
    font-size: 1.1em;
    padding: 5px 0;
}

/* About Section */
.about-section {
    display: flex;
    justify-content: space-around;
    padding: 20px;
}

.about-content {
    text-align: center;
    width: 45%;
}

.about-content img {
    width: 100px;
    height: auto;
    border-radius: 50%;
    margin-bottom: 10px;
}

.about-content p {
    font-size: 0.9em;
    line-height: 1.5;
}

/* Menu Section */
.menu-section {
    background-color: white;
    color: tomato;
    text-align: center;
    padding: 20px;
}

.menu-items {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

.menu-item {
    width: 22%;
    min-width: 120px;
    box-sizing: border-box;
    text-align: center;
    padding: 10px;
}

/* Order Section */
.order-section {
    text-align: center;
    padding: 20px;
}

h2 {
    font-size: 1.5em;
    margin: 20px 0 10px;
    background-color: tomato;
    color: white;
    padding: 5px 0;
}

.order-items,
.delivery-options {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 10px 0;
}

.order-item,
.option {
    margin: 10px;
    padding: 10px 20px;
    background-color: tomato;
    color: white;
    font-size: 1em;
    cursor: pointer;
}

/* Locations Section */
.locationsHeaderCustom {
  display: flex;
  justify-content: center;
  align-items: center;
  background: tomato;
  color: #fff;
  padding: 10px 20px;
  position: relative;
}

.locationsHeaderCustom h1 {
    font-size: 2.5rem;
    font-weight: bold;
}

.locationsHeaderCustom .home-link {
  position: absolute;
  left: 20px;
  color: white;
  font-weight: bold;
  text-decoration: none;
}

.locationsGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    padding: 20px;
    color: #000;
}

.location {
    text-align: center;
    color: #000;
}

.location h2,
.location h3 {
    color: white;
    font-size: 1.5em;
    font-weight: bold;
    margin-bottom: 5px;
}

.location p {
    font-size: 1em;
    margin: 0 0 10px;
}

.location.center {
    grid-column: span 2;
    justify-self: center;
}

.location.philadelphia {
    grid-column: 2;
    grid-row: 1;
}

.location.los-angeles {
    grid-column: 2;
    grid-row: 3;
}

.location img {
    width: 100%;
    max-width: 300px;
    border-radius: 8px;
}

/* Contact Section */
.contact-section {
    display: flex;
    justify-content: space-around;
    padding: 20px;
}

.contact-box {
    text-align: center;
    width: 300px;
    border: 1px solid black;
    padding: 20px;
    box-sizing: border-box;
}

.contact-box h2 {
    font-size: 1.2em;
    margin-bottom: 10px;
}

.contact-box p {
    font-size: 1em;
    margin: 0;
}

/* Optional: Hero Section for Homepage */
.hero {
    text-align: center;
    padding: 40px 20px;
    background-color: #f8f8f8;
}

.hero h2 {
    font-size: 2em;
    margin-bottom: 10px;
}

.hero p {
    font-size: 1.2em;
}

.about-content img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-bottom: 10px;
    object-fit: cover;  /* Makes sure image fills the box and crops excess */
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.menu-item img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 8px;
}
