}

.logo-title {
  display: flex;
  align-items: center;
  gap: 10px;
}

.logo-title img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Contact Info Section */
.contact-section {
    display: flex;
    justify-content: space-around;
    padding: 20px;
    flex-wrap: wrap;
    gap: 20px;
    background-color: #fff8f0;
}

.contact-box {
    text-align: center;
    width: 300px;
    border: 1px solid black;
    padding: 20px;
    box-sizing: border-box;
    background-color: white;
    border-radius: 8px;
}

.contact-box h2 {
    font-size: 1.2em;
    margin-bottom: 10px;
    color: white;
}

.contact-box p {
    font-size: 1em;
    margin: 0;
    color: #333;
}

/* Contact Form Section */
.contact-form-section {
    max-width: 600px;
    margin: 40px auto 60px auto;
    padding: 20px;
    border: 2px solid tomato;
    border-radius: 8px;
    background-color: #fff8f0;
    text-align: left;
}

.contact-form-section h2 {
    text-align: center;
    color: white;
    margin-bottom: 20px;
}

#contactForm {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

#contactForm label {
    font-weight: bold;
    color: #444;
}

#contactForm input,
#contactForm textarea {
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
    box-sizing: border-box;
    resize: vertical;
}

#contactForm button {
    padding: 12px;
    background-color: tomato;
    color: white;
    border: none;
    font-size: 1.1em;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 10px;
}

#contactForm button:hover {
    background-color: #d6482c;
}

.form-status {
    font-weight: bold;
    text-align: center;
    margin-top: 15px;
    min-height: 1.2em;
}

.form-status.success {
    color: green;
}

.form-status.error {
    color: red;
}

/* Responsive */
@media (max-width: 700px) {
    .contact-section {
        flex-direction: column;
        align-items: center;
    }

    .contact-box {
        width: 90%;
    }
}

